.erp-vertical-menu {
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 12px 8px;
  background: white;
  border-right: 1px solid #e0e0e0;

  .menu-item {
    width: 48px;
    height: 48px;
    border: none;
    background: transparent;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.2s;
    display: flex;
    align-items: center;
    justify-content: center;

    .icon {
      font-size: 20px;
    }

    &:hover {
      background: #f5f5f5;
    }

    &.active {
      background: #e3f2fd;
      color: #667eea;
    }
  }
}
